@import "https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap";
body {
  box-sizing: border-box;
  background-color: #333;
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
  font-style: normal;
  font-weight: 400;
  line-height: 1.6;
}

h1 {
  text-align: center;
  letter-spacing: .2rem;
  color: #f0f8ff;
  margin-top: 20px;
  margin-bottom: 20px;
  font-size: 2.4em;
}

form {
  justify-content: center;
  margin-bottom: 40px;
  display: flex;
}

input[name="searchQuery"] {
  color: #333;
  border: none;
  border-radius: 5px 0 0 5px;
  min-width: 200px;
  max-width: 300px;
  padding: 10px 20px;
  font-size: 16px;
  box-shadow: 0 0 5px #0003;
}

form button[type="submit"] {
  color: #f0f8ff;
  cursor: pointer;
  background-color: #6495ed;
  border: none;
  border-radius: 0 5px 5px 0;
  padding: 10px 20px;
  font-size: 16px;
  font-weight: 600;
  box-shadow: 0 0 5px #0003;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px;
  padding: 0 0 30px;
  display: flex;
}

.photo-card img {
  object-fit: cover;
  width: 100%;
  height: 250px;
  display: block;
}

.photo-card {
  cursor: pointer;
  object-fit: cover;
  border-radius: 3px;
  outline: 1px solid #f0f8ff;
  flex-direction: column;
  width: 300px;
  transition: opacity .4s ease-in-out;
  display: flex;
  overflow: hidden;
  box-shadow: 0 0 5px #0003;
}

.photo-card:hover {
  opacity: .6;
}

.info {
  color: #f0f8ff;
  flex-direction: row;
  justify-content: space-evenly;
  font-size: 12px;
  display: flex;
}

.info-item {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.load-more {
  color: #f0f8ff;
  cursor: pointer;
  background-color: #6495ed;
  border: none;
  border-radius: 5px;
  margin: 25px auto;
  padding: 10px 20px;
  font-size: 16px;
  font-weight: 600;
  display: none;
  box-shadow: 0 0 5px #0003;
}
